
.spinner[data-v-132ca718]{position:relative;border:4px solid #41b883;animation:loader 2s infinite ease
}
.loader-inner[data-v-132ca718]{vertical-align:top;display:inline-block;width:100%;background-color:#41b883;animation:loader-inner 2s infinite ease-in
}
@keyframes loader{
0%{transform:rotate(0deg)
}
25%{transform:rotate(180deg)
}
50%{transform:rotate(180deg)
}
75%{transform:rotate(360deg)
}
100%{transform:rotate(360deg)
}
}
@keyframes loader-inner{
0%{height:0%
}
25%{height:0%
}
50%{height:100%
}
75%{height:100%
}
100%{height:0%
}
}



